const styles = {
  '@global': {
    body: {
      margin: 0,
      transition: 'background-color 0.5s ease-in'
    },
    '*': {
      boxSizing: 'border-box'
    },
    '@media (max-width: 768px)': {
      html: {
        fontSize: '14px'
      }
    }
  }
};

export default styles;
